html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  color: #000000;
  background-color: #ffffff;
  font-family: "Gentium Book Plus", serif;
  font-size: 18px;
  margin: 0;
  padding: 0;
}

p {
  font-size: 1.6rem;
  line-height: 2;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  width: 90%;
  max-width: 144rem;
  margin: 0 auto;
}

.header {
  background-color: #33424F;
  position: relative;
  color: #ffffff;
}
.header__logo {
  height: 6rem;
  margin: 0;
  line-height: 6rem;
  font-size: 2.3rem;
  font-weight: 700;
}
@media (min-width: 48em) {
  .header__logo {
    font-size: 2.5rem;
  }
}
.header__banner {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../../../build/img/gallery.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25rem;
  position: relative;
}
@media (min-width: 48em) {
  .header__banner {
    height: 35rem;
  }
}
.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__title {
  color: white;
  font-size: 5rem;
}

.nav__icon {
  cursor: pointer;
  font-size: 2.5rem;
}
.nav__icon--close {
  display: none;
}
@media (min-width: 48em) {
  .nav__icon i {
    display: none;
  }
}
.nav__menu {
  background-color: #33424F;
  position: absolute;
  right: 0;
  left: 0;
  top: 6rem;
  bottom: 100%;
  text-align: center;
  overflow: hidden;
  transition: all 1000ms ease-in-out;
}
@media (min-width: 48em) {
  .nav__menu {
    display: flex;
    justify-content: center;
    gap: 2rem;
    position: unset;
  }
}
.nav__list {
  margin-top: 3rem;
  font-size: 2rem;
}
.nav__list:last-of-type {
  margin-bottom: 3rem;
}
.nav__link {
  color: #ffffff;
}

.active {
  bottom: unset;
  z-index: 1;
}

.search {
  padding: 2rem;
  background-color: #333333;
}

.form {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form label {
  color: #333333;
  display: block;
  text-transform: capitalize;
  font-size: 2.3rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2rem;
}
.form input {
  border-radius: 0.7rem;
  border: 1px solid rgba(0, 0, 0, 0.5);
  font-size: 2rem;
  padding: 0.7rem;
  width: 100%;
  outline: none;
}
@media (min-width: 48em) {
  .form input {
    max-width: 50%;
  }
}

.search__btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}
.search__btn .btn {
  border: none;
  border-radius: 0.7rem;
  cursor: pointer;
  padding: 1rem 1.5rem;
  background-color: #FF812E;
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 2rem 0;
}

.gallery {
  padding: 5rem 0;
}
.gallery__container {
  display: grid;
  justify-items: center;
  gap: 1rem;
  justify-content: center;
}
@media (min-width: 48em) {
  .gallery__container {
    grid-template-columns: repeat(2, 28rem);
  }
}
@media (min-width: 64em) {
  .gallery__container {
    grid-template-columns: repeat(3, 28rem);
  }
}
@media (min-width: 87.5em) {
  .gallery__container {
    grid-template-columns: repeat(4, 28rem);
  }
}
.gallery__container .card {
  background-color: #F0F2F5;
  border-radius: 0.5rem;
  overflow: hidden;
  height: 33rem;
  width: 25rem;
}
.gallery__container .card__image {
  height: 100%;
}
@media (min-width: 30em) {
  .gallery__container .card {
    height: 40rem;
    width: 28rem;
  }
}

.footer {
  margin: 0;
}
.footer .copyright {
  background-color: #33424F;
  color: #ffffff;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  padding: 2rem 0;
  margin: 0;
}